body {
  --main-hover-color: #00acdc;
  --text-color: #000;
  margin: 0;
  padding: 0;
  font-family: Roboto, Arial, sans-serif;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 0;
  padding: 0 50px;
  display: flex;
}

.logo-link {
  line-height: 0;
}

.nav-list {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-list-item:not(:last-child) {
  margin-right: 20px;
}

.nav-link {
  text-align: center;
  color: var(--text-color);
  text-transform: uppercase;
  height: 60px;
  font-size: 12px;
  font-style: normal;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.nav-link:hover {
  color: var(--main-hover-color);
}

.is-active {
  color: var(--main-hover-color);
  position: relative;
}

.is-active:after {
  content: "";
  background: var(--main-hover-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

